module.exports = "<div class=markdown _v-603117a4=\"\"><h1 id=dropdown _v-603117a4=\"\">Dropdown</h1> <hr _v-603117a4=\"\"> <p _v-603117a4=\"\">向下弹出的列表。</p> <h2 id=- _v-603117a4=\"\">何时使用</h2> <p _v-603117a4=\"\">当页面上的操作命令过多时，用此组件可以收纳操作元素。点击或移入触点，会出现一个下拉菜单。可在列表中进行选择，并执行相应的命令。</p> <h2 id=api _v-603117a4=\"\">API</h2> <table _v-603117a4=\"\"> <thead _v-603117a4=\"\"> <tr _v-603117a4=\"\"> <th _v-603117a4=\"\">参数</th> <th _v-603117a4=\"\">说明</th> <th _v-603117a4=\"\">类型</th> <th _v-603117a4=\"\">默认值</th> </tr> </thead> <tbody _v-603117a4=\"\"> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">visible</td> <td _v-603117a4=\"\">菜单是否显示</td> <td _v-603117a4=\"\">boolean</td> <td _v-603117a4=\"\">false</td> </tr> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">value</td> <td _v-603117a4=\"\">菜单按钮文字</td> <td _v-603117a4=\"\">String</td> <td _v-603117a4=\"\">undefined</td> </tr> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">type</td> <td _v-603117a4=\"\">菜单按钮类型，可选值为 <code _v-603117a4=\"\">primary</code> <code _v-603117a4=\"\">default</code> <code _v-603117a4=\"\">success</code> <code _v-603117a4=\"\">info</code> <code _v-603117a4=\"\">warning</code> <code _v-603117a4=\"\">danger</code> 或者不设</td> <td _v-603117a4=\"\">String</td> <td _v-603117a4=\"\">default</td> </tr> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">size</td> <td _v-603117a4=\"\">设置按钮大小，可选值为 <code _v-603117a4=\"\">xsmall</code> <code _v-603117a4=\"\">small</code> <code _v-603117a4=\"\">large</code> 或者不设</td> <td _v-603117a4=\"\">String</td> <td _v-603117a4=\"\">undefined</td> </tr> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">loading</td> <td _v-603117a4=\"\">设置按钮载入状态，存在为 <code _v-603117a4=\"\">true</code>，不存在为 <code _v-603117a4=\"\">false</code>，或直接设置值，如：<code _v-603117a4=\"\">loading=\"true\"</code></td> <td _v-603117a4=\"\">Bool</td> <td _v-603117a4=\"\">false</td> </tr> <tr _v-603117a4=\"\"> <td _v-603117a4=\"\">onclick</td> <td _v-603117a4=\"\"><code _v-603117a4=\"\">click</code> 事件的 handler</td> <td _v-603117a4=\"\">Function</